import { useEffect, useState } from 'react';
import { useSnackbar } from 'notistack';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import Lottie from 'lottie-react';
import { styled, alpha } from '@mui/material/styles';
import {
  Container,
  Stack,
  Card,
  Typography,
  Button,
  Box,
  Grid,
  IconButton
} from '@mui/material';
import { PATH_DASHBOARD } from '../../routes/paths';
import useSettings from '../../hooks/useSettings';
import useAuth from '../../hooks/useAuth';
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import Iconify from '../../components/Iconify';

import animationData from '../../utils/loader.json';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

const STEPS = [
  { title: 'Copy Account', desc: 'Copy any of your unique account numbers below.', icon: 'eva:copy-fill', color: 'primary' },
  { title: 'Make Transfer', desc: 'Transfer money from your bank app to the account.', icon: 'eva:diagonal-arrow-right-up-fill', color: 'info' },
  { title: 'Instant Credit', desc: 'Your wallet balance will be updated automatically.', icon: 'eva:flash-fill', color: 'warning' },
];

const BankCard = styled(Card)(({ theme }) => ({
  position: 'relative',
  padding: theme.spacing(3),
  background: `linear-gradient(135deg, ${theme.palette.primary.darker} 0%, ${theme.palette.primary.main} 100%)`,
  color: theme.palette.common.white,
  borderRadius: 16,
  overflow: 'hidden',
  boxShadow: theme.customShadows.z24,
  '&:before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    background: 'url("/assets/icons/glass/ic_glass_buy.png") center center / cover no-repeat', // Or a subtle pattern
    opacity: 0.1,
  },
  '&:after': {
    content: '""',
    position: 'absolute',
    top: -50,
    right: -50,
    width: 200,
    height: 200,
    background: 'radial-gradient(circle, rgba(255,255,255,0.1) 0%, transparent 70%)',
    borderRadius: '50%',
  }
}));

const InfoRow = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  padding: theme.spacing(1, 0),
  borderBottom: `1px solid ${alpha(theme.palette.common.white, 0.1)}`,
  '&:last-child': {
    borderBottom: 'none',
  }
}));

const CopyButton = styled(Button)(({ theme }) => ({
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  color: theme.palette.common.white,
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  }
}));

const ProcessingCard = styled(Card)(({ theme }) => ({
  padding: theme.spacing(3, 2),
  textAlign: 'center',
  backgroundColor: theme.palette.background.neutral,
  border: `1px dashed ${theme.palette.divider}`,
  borderRadius: 12,
}));

// ----------------------------------------------------------------------


export default function Automated() {
  const { themeStretch } = useSettings();
  const { setting, user } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [banks, setBanks] = useState([]);

  useEffect(() => {
    const fetchBanks = async () => {
      try {
        setLoading(true);
        const accessToken = localStorage.getItem('accessToken');
        const response = await axios.get(`/api/check/banks/user/gstar/${accessToken}/secure/this/site/here`)
        setBanks(response.data.banks || []);
      } catch (error) {
        console.error('Error fetching Banks:', error);
      }
      finally {
        setLoading(false);
      }
    }

    fetchBanks();
  }, []);

  return (
    <Page title="Automated Bank Transfer">
      {loading ? (
        <Box sx={{ textAlign: 'center', mt: 10 }}>
          <Lottie animationData={animationData} style={{ maxWidth: 300, margin: 'auto' }} />
          <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>Fetching Banks....</Typography>
        </Box>
      ) : (
        <Container maxWidth={themeStretch ? false : 'lg'}>
          <HeaderBreadcrumbs
            heading="Add Money"
            links={[
              { name: 'Dashboard', href: PATH_DASHBOARD.root },
              { name: 'Payment' },
              { name: 'Bank Transfer' },
            ]}
          />

          <Stack spacing={4}>
            {/* Instructions Section */}
            <Box>
              <Typography variant="h4" sx={{ mb: 1 }}>How to Fund Your Wallet</Typography>
              <Typography variant="body2" sx={{ color: 'text.secondary', mb: 3 }}>
                Follow these simple steps to credit your wallet instantly.
              </Typography>

              <Grid container spacing={3}>
                {STEPS.map((step, index) => (
                  <Grid item xs={12} sm={4} key={index}>
                    <Stack direction="row" spacing={2} alignItems="center">
                      <Box sx={{
                        width: 48,
                        height: 48,
                        borderRadius: 1.5,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        bgcolor: (theme) => alpha(theme.palette[step.color].main, 0.1),
                        color: (theme) => theme.palette[step.color].main,
                      }}>
                        <Iconify icon={step.icon} width={24} height={24} />
                      </Box>
                      <Box>
                        <Typography variant="subtitle2">{step.title}</Typography>
                        <Typography variant="caption" sx={{ color: 'text.secondary' }}>{step.desc}</Typography>
                      </Box>
                    </Stack>
                  </Grid>
                ))}
              </Grid>
            </Box>

            {/* Bank Cards Section */}
            <Grid container spacing={3}>
              {banks.length > 0 ? (
                banks.map((bank) => (
                  <Grid item xs={12} md={6} key={bank.name}>
                    {bank.accountType ? (
                      <ProcessingCard>
                        <Stack alignItems="center" spacing={2}>
                          <Box sx={{
                            width: 56,
                            height: 56,
                            borderRadius: '50%',
                            bgcolor: 'primary.lighter',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                          }}>
                            <Iconify icon="eva:settings-2-fill" width={32} height={32} sx={{ color: 'primary.main' }} />
                          </Box>
                          <Typography variant="subtitle1">Setting up your {bank.name} Account</Typography>
                          <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                            We're preparing your unique bank details. Please check back in a moment!
                          </Typography>
                        </Stack>
                      </ProcessingCard>
                    ) : (
                      <BankCard>
                        <Stack spacing={3}>
                          <Stack direction="row" justifyContent="space-between" alignItems="center">
                            <Box>
                              <Typography variant="overline" sx={{ opacity: 0.8, display: 'block', mb: 0.5 }}>Bank Name</Typography>
                              <Typography variant="h6" sx={{ color: 'white' }}>{bank.name}</Typography>
                            </Box>
                            <Box sx={{ p: 1, bgcolor: 'rgba(255,255,255,0.2)', borderRadius: 1 }}>
                              <Iconify icon="eva:award-fill" width={24} height={24} />
                            </Box>
                          </Stack>

                          <Box>
                            <Typography variant="overline" sx={{ opacity: 0.8, display: 'block', mb: 0.5 }}>Account Number</Typography>
                            <Stack direction="row" alignItems="center" spacing={1}>
                              <Typography variant="h3" sx={{ letterSpacing: 2 }}>{bank.account}</Typography>
                              <CopyToClipboard
                                text={bank.account}
                                onCopy={() => enqueueSnackbar(`${bank.name} Account Copied!`)}
                              >
                                <IconButton size="small" sx={{ color: 'white', bgcolor: 'rgba(255,255,255,0.1)' }}>
                                  <Iconify icon="eva:copy-fill" width={20} height={20} />
                                </IconButton>
                              </CopyToClipboard>
                            </Stack>
                          </Box>

                          <Stack direction="row" justifyContent="space-between" alignItems="flex-end">
                            <Box>
                              <Typography variant="overline" sx={{ opacity: 0.8, display: 'block', mb: 0.5 }}>Account Name</Typography>
                              <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>
                                {setting?.general?.app_name} - {user?.username}
                              </Typography>
                            </Box>
                            <Box sx={{ textAlign: 'right' }}>
                              <Typography variant="caption" sx={{ opacity: 0.8, display: 'block' }}>Fee</Typography>
                              <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>{bank.charges}</Typography>
                            </Box>
                          </Stack>
                        </Stack>
                      </BankCard>
                    )}
                  </Grid>
                ))
              ) : (
                <Grid item xs={12}>
                  <Card sx={{ p: 5, textAlign: 'center', bgcolor: 'background.neutral' }}>
                    <Box sx={{ mb: 2, display: 'inline-flex', p: 2, bgcolor: 'action.hover', borderRadius: '50%' }}>
                      <Iconify icon="eva:alert-triangle-fill" width={48} height={48} sx={{ color: 'text.disabled' }} />
                    </Box>
                    <Typography variant="h6" sx={{ color: 'text.secondary' }}>
                      No accounts available right now.
                    </Typography>
                    <Typography variant="body2" sx={{ color: 'text.disabled' }}>
                      Please refresh or try again in a few minutes.
                    </Typography>
                  </Card>
                </Grid>
              )}
            </Grid>

            {/* Support/Security Footer */}
            <Box sx={{
              p: 3,
              borderRadius: 2,
              bgcolor: 'background.neutral',
              border: theme => `1px solid ${theme.palette.divider}`,
              textAlign: 'center'
            }}>
              <Stack direction="row" spacing={1} justifyContent="center" alignItems="center">
                <Iconify icon="eva:lock-fill" sx={{ color: 'success.main' }} />
                <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                  Secured by {setting?.general?.app_name || 'Our'} Infrastructure
                </Typography>
              </Stack>
              <Typography variant="caption" sx={{ color: 'text.secondary', mt: 1, display: 'block' }}>
                Payments are processed through secure gateways and credited instantly to your wallet.
              </Typography>
            </Box>
          </Stack>
        </Container>
      )}
    </Page>
  );
}
